import React, { useState, useEffect } from 'react';
import {Redirect, useHistory } from 'react-router-dom';

import {Image} from "./View"

function Create() {

    const [fields, setFields] = useState({});
    const [data, setData] = useState("s");

    const history = useHistory();

    var handleChange = (key) => {
    
        return (e) => {
            console.log(e.target.value);

            var toMerge = {};
            toMerge[key]=e.target.value;

            setFields({...fields, ...toMerge});
        }
    }

    useEffect(()=> {
        console.log(fields);
        setData(btoa(JSON.stringify(fields)));
    }, [fields])

    var onClick = (e) => {
        history.push(`/view?data=${data}`);
    }


    return (
        <div>
            <div>Create</div>
            <Input label="First Name" handleChange={handleChange("fn")}/>
            <Input label="Last Name" handleChange={handleChange("ln")}/>
            <Input label="DOB Name" handleChange={handleChange("dob")}/>
            <button onClick={onClick}>Create Pass</button>
            <Image data={data} />
        </div>

    );

}

function Input(props) {
    return (
        <div>
            <label>{props.label}</label>
            <input type="text" onChange={props.handleChange}/>
        </div>

    );
}

export default Create;